import React, { Component } from 'react';
import axios from 'axios';
import './App.css';

class ComboViewer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            gameName: '',
            playerName: '',
            comboName: '',
            comboInput: '',
            combo: ''
        }
    }

    // pushRequest = () => {
    //     var nameGame = document.getElementById('nameGame').value;
    //     var nameCharacter = document.getElementById('nameCharacter').value;
    //     var nameCombo = document.getElementById('nameCombo').value;
    //     var inputCombo = document.getElementById('inputCombo').value;

    //     axios.post(`/addGame/${this.state.userName}/${this.state.gameName}/${this.state.player}`);
    //     axios.post(`/addPlayer/${this.state.playerName}`);
    //     axios.post(`/addCombo/${this.state.comboName}/${this.state.comboInput}`);
    // }


    // setStates = (event) => {
    //     this.setState({ [event.target.name]: event.target.value });
    // }

    // handleSubmit = (event) => {
    //     console.log('A game name was submitted: ' + this.state.gameName);
    //     console.log('A player name was submitted: ' + this.state.playerName);
    //     console.log('A combo name was submitted: ' + this.state.comboName);
    //     console.log('A combo was submitted: ' + this.state.comboInput);
    //     event.preventDefault();
    // }

    render() {
        return (
            <div className="body">
                <form onSubmit={this.handleSubmit} className="form-inline">
                    <div className="container">
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <h1>Combo Viewer</h1>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <input type="radio" name="gameName" target='one' className="form-control inputArea" onChange={this.setStates} gameName={this.state.gameName} id="nameGame" placeholder="Game *"></input>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <input type="radio" name="playerName" target='one' className="form-control inputArea" onChange={this.setStates} playerName={this.state.playerName} id="nameCharacter" placeholder="Character *"></input>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <input type="radio" name="comboName" target='one' className="form-control inputArea" onChange={this.setStates} comboName={this.state.combo} id="nameCombo" placeholder="Combo Name *"></input>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <input type="radio" name="comboInput" target='one' className="form-control inputArea" onChange={this.setStates} comboInput={this.state.combo} id="inputCombo" placeholder="Combo Input *"></input>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <input type="submit" value="Submit" onClick={this.pushRequest} />
                                </label>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

export default ComboViewer;