import React, { Component } from 'react';
import axios from 'axios';
import './App.css';

class User extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
            userPassword: ''

        }
    }

    pushRequest = () => {
        axios.post(`/addUser/${this.state.userName}/${this.state.userPassword}`);
    }

    setStates = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    handleSubmit = (event) => {
        console.log('A user name was submitted: ' + this.state.userName);
        console.log('A password was submitted: ' + this.state.userPassword);
        event.preventDefault();
    }

    render() {
        return (
            <div className="body">
                <form onSubmit={this.handleSubmit} className="form-inline">
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <h1>WOMBO COMBO SHOMBO!!!</h1>
                                </label>
                            </div>
                        </div>
                        <br /><br /><br />
                        <div className="row">
                            <div className="col-6">
                                <label>
                                    <h4>Sign Up</h4>
                                </label>                
                            </div>
                            <div className="col-6">
                                <label>
                                    <h4>Log In</h4>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-6">
                                <label>
                                    <input type="text" name='userName' target='one' className="form-control inputArea" onChange={this.setStates} userName={this.state.userName} id="userName" placeholder="Username *"></input>
                                </label>
                            </div>
                            <div className="col-6">
                                <label>
                                    <input type="text" name='userName' target='one' className="form-control inputArea" id="userName" placeholder="Username *"></input>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-6">
                                <label>
                                    <input type="text" name='userPassword' target='one' className="form-control inputArea" onChange={this.setStates} userName={this.state.userPassword} id="userPassword" placeholder="Password *"></input>
                                </label>
                            </div>
                            <div className="col-6">
                                <label>
                                    <input type="text" name='userPassword' target='one' className="form-control inputArea" id="userPassword" placeholder="Password *"></input>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-6">
                                <label>
                                    <input type="submit" value="Submit" onClick={this.pushRequest} />
                                </label>
                            </div>
                            <div className="col-6">
                                <label>
                                    <input type="submit" value="Submit" />
                                </label>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

export default User;